import React from 'react';

const products = (props) => {


    const products = props.products.map((product, i) => {
        return(
            <section className="module bg-dark-60 portfolio-page-header" data-background={product.url_img} id={product.shortcut}>
            <div className="container">
                <div className="row">
                <div className="col-sm-6 col-sm-offset-3">
                    <h2 className="module-title font-alt">{product.title}</h2>
                    <div className="module-subtitle font-serif">{product.description}</div>
                </div>
                </div>
            </div>
            </section>
        )
    })

    return(
        <div>
            {products}
        </div> 
    )
}

export default products;