import React, { Component } from 'react'

export class EstimateForm extends Component {
   
    constructor(props) {
        super(props);
        this.state = {value: ''};
    
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }
    
      handleChange(event) {
        this.setState({value: event.target.value});
      }
    
      handleSubmit(event) {
        alert('A name was submitted: ' + this.state.value);
        event.preventDefault();
      }
    
      render() {
        return (
                <form onSubmit={this.handleSubmit} className="form" role="form">
                  <div className="form-group">
                    <input value={this.state.value} onChange={this.handleChange} className="form-control input-lg" type="text" placeholder="Large input"/>
                  </div>
                  <div className="form-group">
                    <input className="form-control" type="text" placeholder="Default input"/>
                  </div>
                  <div className="form-group">
                    <input className="form-control input-sm" type="text" placeholder="Small input"/>
                  </div>
                  <div className="form-group">
                    <input className="form-control" type="text" placeholder="Disabled input" disabled=""/>
                  </div>
                  <div className="form-group">
                    <input className="form-control" type="text" placeholder="Readonly input" readonly=""/>
                  </div>
                  <textarea className="form-control" rows="7" placeholder="Textarea"></textarea>
                  <label>
                    
                    </label>
                    <input type="submit" value="Submit" />
                </form>
        );
      }
}

export default EstimateForm
